<template>
  <div>
    <!-- <gmap-map :zoom="zoom" :center="center" style="width: 100%; height: 600px">
      <gmap-marker
        :icon="img"
        :key="index"
        :animation="ani"
        v-for="(m, index) in locationMarkers"
        :position="m.position"
        @click="center = m.position"
      ></gmap-marker>

      </gmap-map> -->
    <gmap-map style="width: 100%; height: 600px" :zoom="3" :center="center">
      <gmap-marker
        v-for="(marker, index) in markers"
        :key="index"
        :position="marker.position"
      ></gmap-marker>
      <gmap-marker
        v-if="this.place"
        label="&#x2605;"
        :position="{
          lat: this.place.geometry.location.lat(),
          lng: this.place.geometry.location.lng(),
        }"
      ></gmap-marker>
    </gmap-map>
    <gmap-autocomplete
      placeholder="This is a placeholder text"
      @place_changed="setPlace"
    /><button @click="usePlace">Add</button>
    <ul>
      <li :v-for="city in markers">{{ city }}</li>
    </ul>
  </div>
</template>

<script>
import imgpath from "@/assets/logo.png";

export default {
  name: "AddGoogleMap",
  data() {
    return {
      markers: [
        {
          position: {
            lat: 10.0,
            lng: 10.0,
          },
        },
        {
          position: {
            lat: 11.0,
            lng: 11.0,
          },
        },
        {
          position: {
            lat: 37.0,
            lng: 127.0,
          },
        },
      ],
      place: null,
      center: { lat: 37.0, lng: 127.0 },
      zoom: 5,
      img: {
        url: imgpath,
        scaledSize: { width: 20, height: 20 },
      },
      ani: 1,
      // center: seoul,
      locationMarkers: [
        {
          // position: seoul,
        },
      ],
    };
  },
  mounted() {
    console.log("mount");
    // console.log(this.center);
  },
  methods: {
    setDescription(description) {
      this.description = description;
    },
    setPlace(place) {
      // 자동완료 선택
      this.place = place;
      console.log("ggg");
    },
    usePlace(place) {
      // 검색
      console.log(place);
      if (this.place) {
        this.markers.push({
          position: {
            lat: this.place.geometry.location.lat(),
            lng: this.place.geometry.location.lng(),
          },
        });
        // 중심을 검색한 장소로 이동.
        this.center = {
          lat: this.place.geometry.location.lat(),
          lng: this.place.geometry.location.lng(),
        };
        this.place = null;
      }
    },
  },
};
</script>
